import React from "react"
import { Link } from "gatsby"
import "./base.css"

const Projects = () => {
  const projects = [
    {
      id: 1,
      title: "MyMentor",
      subtitle: "Native mobile app to create mentorships",
      description: "Xamarin Forms frontend with .NET backend",
      url: "mymentor",
      code: "https://github.com/frederickbyte/MyMentor",
    },
    {
      id: 2,
      title: "Personal Site",
      subtitle: "My personal website (this one!)",
      description: "React frontend powered with Gatsby",
      url: "/",
      code: "https://github.com/frederickbyte/frederickbyte.github.io",
    },
    {
      id: 3,
      title: "HPC Budget Manager",
      subtitle: "Web application for Healing Place Church",
      description: "AngularJS frontend with Material UI and Kendo UI components",
      url: "hpcbudgeting",
      code: "https://andrewstolzle.com",
    },
  ]
  return (
    <div className="flex-column">
      <h3 className="recent-proj-header">Recent Projects</h3>
      {projects.map(p => {
        return (
          <div className="nes-container is-dark with-title" key={p.id}>
            <p className="title green-text">{p.title}</p>
            <ul className="proj-details-list">
              <li>{p.subtitle}</li>
              <li>{p.description}</li>
            </ul>
            <div style={{ display: `flex`, alignItems: `center`, flexWrap: `wrapf` }}>
              <a href={p.code} rel="noopener noreferrer" className="nes-btn is-primary">
                CODE
              </a>
              <Link to={p.url} className="nes-btn">
                INFO
              </Link>
            </div>
          </div>
        )
      })}
    </div>
  )
}

export default Projects
